@import url(https://fonts.googleapis.com/css2?family=Sriracha&display=swap);
body {
  margin: 0;
  font-family:'Sriracha', cursive, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ecebec;
  background-image: url("data:image/svg+xml,%3Csvg width='42' height='44' viewBox='0 0 42 44' xmlns='http://www.w3.org/2000/svg'%3E%3Cg id='Page-1' fill='none' fill-rule='evenodd'%3E%3Cg id='brick-wall' fill='%23000000' fill-opacity='0.09'%3E%3Cpath d='M0 0h42v44H0V0zm1 1h40v20H1V1zM0 23h20v20H0V23zm22 0h20v20H22V23z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

h1{
  font-size: 50px !important;
  text-align: center;
  letter-spacing: 2px;
  font-style: italic;
}

.image-container{
  /* margin: 15px 22%; */
  background-color:whitesmoke;


}
.image-container img{
  width: 100%;
  margin-bottom: 18px ;
  box-shadow: 0px 10px 10px 2px rgba(18, 18, 19, 0.74);
}
.loading{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:rgba(233, 230, 230, 0.671);
}

.mh8 { margin-left: 20%; margin-right: 20%; }

.shadow-6 { box-shadow: 0px 7px 25px 20px rgba(124, 118, 118, 0.815); }

@media screen and (max-width:600px){
  .mh8 { margin-left: 10px; margin-right: 10px; }
  h1{font-size: 22px !important;}
  
}
